import React from 'react'
import { Formik } from 'formik'
import * as yup from 'yup'
import Form from 'react-bootstrap/Form'
import Col from 'react-bootstrap/Col'
import Button from 'react-bootstrap/Button'

const schema = yup.object().shape({
  name: yup.string().required('This is a required question'),
  date: yup.date().required('This is a required question'),
  phone: yup.string().required('This is a required question'),
  email: yup.string().email().required('This is a required question'),
  address: yup.string().required('This is a required question'),
  how: yup.string().required('This is a required question'),
})

function RegisterPersonPage() {
  return (
    <Formik
      validationSchema={schema}
      onSubmit={() => console.log('amego')}
      initialValues={{
        name: '',
        date: '',
        phone: '',
        email: '',
        address: '',
        how: '',
      }}
    >
      {({
        handleSubmit,
        handleChange,
        handleBlur,
        values,
        touched,
        errors,
      }) => (
        <Form noValidate onSubmit={handleSubmit}>
          <Form.Row>
            <Form.Group as={Col} md='4' controlId='validationFormikName'>
              <Form.Label>الاسم الثلاثي</Form.Label>
              <Form.Control
                type='text'
                placeholder='Your answer'
                name='name'
                value={values.name}
                onChange={handleChange}
                onBlur={handleBlur}
                isValid={touched.name && !errors.name}
                isInvalid={touched.name && !!errors.name}
              />
              <Form.Control.Feedback>Looks good!</Form.Control.Feedback>
              <Form.Control.Feedback type='invalid'>
                {errors.name}
              </Form.Control.Feedback>
            </Form.Group>
          </Form.Row>
          <Form.Row>
            <Form.Group as={Col} md='4' controlId='validationFormikDate'>
              <Form.Label>تاريخ الميلاد</Form.Label>
              <Form.Control
                type='date'
                name='date'
                value={values.date}
                onChange={handleChange}
                onBlur={handleBlur}
                isValid={touched.date && !errors.date}
                isInvalid={touched.date && !!errors.date}
              />
              <Form.Control.Feedback>Looks good!</Form.Control.Feedback>
              <Form.Control.Feedback type='invalid'>
                {errors.date}
              </Form.Control.Feedback>
            </Form.Group>
          </Form.Row>
          <Form.Row>
            <Form.Group as={Col} md='4' controlId='validationFormikPhone'>
              <Form.Label>رقم الهاتف</Form.Label>
              <Form.Control
                type='text'
                placeholder='Your answer'
                name='phone'
                value={values.phone}
                onChange={handleChange}
                onBlur={handleBlur}
                isValid={touched.phone && !errors.phone}
                isInvalid={touched.phone && !!errors.phone}
              />
              <Form.Control.Feedback>Looks good!</Form.Control.Feedback>
              <Form.Control.Feedback type='invalid'>
                {errors.phone}
              </Form.Control.Feedback>
            </Form.Group>
          </Form.Row>
          <Form.Row>
            <Form.Group as={Col} md='4' controlId='validationFormikEmail'>
              <Form.Label>البريد الإلكتروني</Form.Label>
              <Form.Control
                type='text'
                placeholder='Your answer'
                name='email'
                value={values.email}
                onChange={handleChange}
                onBlur={handleBlur}
                isValid={touched.email && !errors.email}
                isInvalid={touched.email && !!errors.email}
              />
              <Form.Control.Feedback>Looks good!</Form.Control.Feedback>
              <Form.Control.Feedback type='invalid'>
                {errors.email}
              </Form.Control.Feedback>
            </Form.Group>
          </Form.Row>
          <Form.Row>
            <Form.Group as={Col} md='4' controlId='validationFormikAddress'>
              <Form.Label>مكان السكن</Form.Label>
              <Form.Control
                type='text'
                placeholder='Your answer'
                name='address'
                value={values.address}
                onChange={handleChange}
                onBlur={handleBlur}
                isValid={touched.address && !errors.address}
                isInvalid={touched.address && !!errors.address}
              />
              <Form.Control.Feedback>Looks good!</Form.Control.Feedback>
              <Form.Control.Feedback type='invalid'>
                {errors.address}
              </Form.Control.Feedback>
            </Form.Group>
          </Form.Row>
          <Form.Row>
            <Form.Group as={Col} md='4' controlId='validationFormikHow'>
              <Form.Label>كيف عرفت عن المنصة ؟</Form.Label>
              <Form.Check>
                <Form.Check.Input
                  type='radio'
                  name='how'
                  value='سوشال ميديا'
                  id='سوشال ميديا'
                  onChange={handleChange}
                  onBlur={handleBlur}
                />
                <Form.Check.Label>سوشال ميديا</Form.Check.Label>
              </Form.Check>
              <Form.Check>
                <Form.Check.Input
                  type='radio'
                  name='how'
                  value='أصدقاء و أقارب'
                  id='أصدقاء و أقارب'
                  onChange={handleChange}
                  onBlur={handleBlur}
                />
                <Form.Check.Label>أصدقاء و أقارب</Form.Check.Label>
              </Form.Check>
              <Form.Check>
                <Form.Check.Input
                  type='radio'
                  name='how'
                  value='عن طريق البحث'
                  id='عن طريق البحث'
                  onChange={handleChange}
                  onBlur={handleBlur}
                />
                <Form.Check.Label>عن طريق البحث</Form.Check.Label>
              </Form.Check>
              <Form.Check id='مؤسسات المجتمع المدني' bsPrefix>
                <Form.Check.Input
                  type='radio'
                  name='how'
                  value='مؤسسات المجتمع المدني'
                  onChange={handleChange}
                  onBlur={handleBlur}
                  bsPrefix
                  isValid={touched.how && !errors.how}
                  isInvalid={touched.how && !!errors.how}
                />
                <Form.Check.Label>مؤسسات المجتمع المدني</Form.Check.Label>
                <Form.Control.Feedback>Looks good!</Form.Control.Feedback>
                <Form.Control.Feedback type='invalid'>
                  {errors.how}
                </Form.Control.Feedback>
              </Form.Check>
            </Form.Group>
          </Form.Row>
          <Button type='submit'>Submit form</Button>
        </Form>
      )}
    </Formik>
  )
}

export default RegisterPersonPage
